body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-family: "Helvetica", Arial, sans-serif;
}
.App {
  display: flex;
}
.container {
  padding: 0;
}
form {
  height: 100vh;
  background-color: #669999;
  padding: 50px;
}
input[type="text"] {
  margin-bottom: 20px;
  border: none;
  border-bottom: 1px solid white;
  border-radius: 0;
  background-color: transparent;
  color: #fff !important;
}
::-webkit-input-placeholder {
  color: #fff !important;
}
form div {
  text-align: center;
}
button {
  color: #fff !important;
  margin-top: 20px;
  background-color: transparent !important;
  border-width: 2px !important;
}
button:hover {
  background-color: #6c757d !important;
  border: none;
}
input[type="text"]:focus {
  background-color: transparent;
  border: none;
  color: #fff !important;
  box-shadow: none;
  border-bottom: 1px solid #fff;
}
.cards {
  text-align: center;
}
.weather {
  color: #669999;
}
